#header {
    padding: 0 8.333333vw;
  
    height: 64px;
  
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header-left {
    display: flex;
  
    align-items: center;
  }
  
  .header-left {
    cursor: pointer;
  }
  
  .header-left-logo-icon {
    margin: 3px;
  
    width: 18px;
    height: 18px;
  
    background-image: url(asset/edit_fill_black.png);
    background-position: 50% 50%;
    background-size: 100% 100%;
  }
  
  .header-left-logo-text {
    margin-left: 10px;
  
    font-family: 'GimhaeGaya';
    font-size: 20px;
    font-weight: 400;
    letter-spacing: -0.4px;
  }
  
  .header-right {
    display: flex;
  
    align-items: center;
  }
  
  .header-icon-box {
    border-radius: 24px;
  
    width: 24px;
    height: 24px;
  }
  
  .header-icon-box:hover {
    cursor: pointer;
    background-color: rgba(240, 240, 240, 0.3);
  }
  
  .header-search-icon {
    margin: 1.5px;
  
    width: 21px;
    height: 21px;
  
    background-image: url(asset/search.png);
    background-position: 50% 50%;
    background-size: 100% 100%;
  }
  
  .header-search-box {
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 43px;
  
    padding-left: 12px;
    padding-right: 10px;
  
    width: 223px;
    height: 30px;
  
    display: flex;
    align-items: center;
  }
  
  .header-search-input {
    border: none;
    outline: none;
    background: none;
  
    font-size: 14px;
    font-weight: 400;
  
    flex: 1;
  }